import GridBox from './GridBox'

interface Content {
    photo: string
}

interface Props {
    GridContent: Array<Content>
}

const Grid:React.FC<Props> = (props) => {
    return (
        <ul>
            {props.GridContent.map((content: Content, index) => {
                <GridBox BoxContent=""></GridBox>            
            })}
        </ul>
    )
};

export default Grid;